export class Cell {
    constructor(value, color, id) {
        this.value = value;
        this.color = color;
        this.id = id;
    }
    getId() {
        return this.id;
    }
    getValue() {
        return this.value;
    }
    setValue() {
        this.value = this.value;
    }
    getColor() {
        return this.color;
    }
    setColor(color) {
        this.color = color;
    }
    toString() {
        return this.getValue().toString();
    }
}
